.page-footer__container {
  margin-bottom: 57px;
  @media screen and (max-width: 480px) {
    margin-bottom: 16px;
  }

  @media screen and (min-width: 768px) {
    padding-top: 35px;
  }

  @media screen and (min-width: 1344px) {
    padding: 0;
  }
}

.page-footer {
  @media screen and (max-width: 480px) {
    padding-top: 28px;
  }

  @media screen and (min-width: 768px) {
    margin-bottom: 40px;
  }
}

.page-footer__list {
  margin: 0;
  padding-top: 56px;
  list-style: none;
  display: flex;
  justify-content: space-between;
  flex-direction: row-reverse;
  max-width: 65%;

  @media screen and (max-width: 480px) {
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  @media screen and (min-width: 768px) {
    display: flex;
    justify-content: space-between;
    flex-direction: row-reverse;
    max-width: 80%;
  }

  @media screen and (min-width: 1344px) {
    margin: 0;
    padding-top: 56px;
    list-style: none;
    display: flex;
    justify-content: space-between;
    flex-direction: row-reverse;
    max-width: 68%;
  }
}

.page-footer__iteam {
  display: flex;
  justify-content: center;
  align-items: center;

  @media screen and (max-width: 480px) {
    &:not(:last-of-type) {
      margin-bottom: 25px;
    }
  }

  @media screen and (min-width: 768px) {
    display: flex;
    justify-content: flex-start;
  }
}

.page-footer__link {
  position: relative;
  display: flex;
  align-items: center;
  font-weight: 600;
  font-size: 30px;
  line-height: 37px;
  letter-spacing: 0.08em;
  color: $primary-black-color;
  text-decoration: none;

  &:not(:last-of-type) {
    margin-right: 16px;
  }

  @media screen and (max-width: 480px) {
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    display: flex;
    align-items: center;

    &:not(:last-of-type) {
      margin-bottom: 25px;
    }
  }
  @media screen and (min-width: 768px) {
    font-size: 20px;
    &:not(:last-of-type) {
      margin-right: 60px;
    }
  }

  @media screen and (min-width: 1344px) {
    font-size: 25px;
  }
}

.page-footer__link--pseudo {
  &::after {
    content: '';
    width: 0.063rem;
    height: 1.813rem;

    top: 0.625rem;
    right: -1.875rem;
    border: 1px solid #b9b9b9;
    margin-left: 16px;
  }
}

.logo {
  &__svg {
    width: 128px;
    height: 45px;
    fill: black;
    @media screen and (max-width: 480px) {
      width: 80px;
      height: 30px;
    }
  }
  &--white {
    fill: $primary-white-color;
  }
  &__icon {
    fill: black;
    @media screen and (min-width: 768px) {
      fill: $accent-color;
    }
  }
}

.footer-svg {
  width: 30px;
  height: 30px;
  fill: $accent-color;
  margin-right: 10px;
}

.div-sub {
  padding-top: 20px;
  padding-bottom: 20px;
  background-color: $primary-black-color;
}

.div-sub__protected {
  margin: 0;
  font-weight: 500;
  font-size: 14px;
  line-height: 28px;

  display: flex;
  align-items: center;

  color: #b9b9b9;
}
